input, select {
  margin: 0 16px;
  width: 220px;
}
button {
  background-color: black;
  color: white;
  padding: 4px 8px;
  border-radius: 2px;
  min-width: 80px;
  margin-right: 8px;
  &:disabled{
    background-color: rgb(138, 138, 138);
  }
}
.btn {
  display: flex;
  justify-content: space-between;
  margin-top: 16px;
  margin-bottom: 16px;
}

.content-container{
  padding: 2rem 16px;
  .content{
    display: flex;
    align-items: center;
    margin: 5px 0;
    p{
        font-size: 16px;
        min-width: 200px;
    }
    .input-group{
        display: flex;
        align-items: center;
        label{
            font-size: 12px;
            margin-right: 16px;
        }
        input{
            width: 250px;
        }
        .input{
            margin: 0 0 0 16px;
            width: 250px;
        }
        .btn{
            margin-top: 8px;
            margin-bottom: 8px;
        }
    }
  }
}

.operations{
  border-top: 1px solid black;
}