.custom_input {
    position: relative;
    display: inline-block;
    width: 100%;
}
.padding_input {
    padding-right: 50px;
}
.invalid_input {
    border: 1px solid red !important;
}
input:disabled { background-color: #F7F7F7 !important; color: #000000;}
.icon-close {
    position: absolute;
    right: 0px;
    top: 12px;
    width: 16px;
    height: 16px;
    background: url("../../../assets/images/ico_del_inp.svg") no-repeat center center;
    margin-right: 20px;
}